<template>
  <div>
    this is grandson's 地盘
    <p>miss:{{coo}}</p>
    <button @click="starUp">这是grandson的地盘,点击发送火箭</button>
    <br><br>
    <button @click="succuse">这是grandson的地盘,点击成功</button>
  </div>
</template>
<script>
export default {
  inheritAttrs: false,
  props: {
    coo: {
      type: String,
      default: ""
    }
  },
  data () {
    return {}
  },
  mounted () {
    this.overallMixin()
  },
  created () { },
  methods: {
    starUp () {
      this.$emit('up')
    },
    succuse () {
      this.$emit('change')
    }
  }
}
</script>
