<template>
<div>
  son
  <p>{{value2}}</p>
  msg:{{foo}}
  <p>attrs:{{$attrs}}</p>
  <!-- 未被子组件使用props接收的的值,可以用$attrs来接收(父组件传了两个值过来 foo 和coo) -->
  <button @click="this.overallMixin">在son的地盘,使用全局mixin</button>
  <br><br>
  <grand-son v-bind="$attrs" v-on="$listeners"></grand-son>
  <!-- $listeners 将father的方法传个grandson 可以同时接收多个方法 -->
</div>
</template>
<script>
import GrandSon from '@/componet2/grandson'
export default {
  /* 在子组件中添加inheritAttrs可以将div标签的 coo="miss" 隐藏，inheritAttrs默认值为true，将其设为false即可隐藏。注意：将inheritAttrs设为false并不会影响$attrs的使用。 */
  inheritAttrs: false,
  components: {
    GrandSon
  },
  props: {
    foo: {
      type: String,
      default: ""
    }
  },
  data () {
    return {}
  },
  created () { },
  methods: {}
}
</script>
