<template>
<div>
  father
  他获得冠军的数量{{champion}} <!-- 局部mixins的变量 -->
  <button @click="this.mixinMethod">点击触发局部mixin</button>
  <br>
  -------------------<br>
  <son 
  :foo="msg" 
  :coo="miss" 
  v-on:up="recive"
  @change="change"></son>
  <!-- 将recive方法传递到grandson页面-->
</div>
</template>
<script>
import mixin from '@/mixins/mixins'
import Son from '@/componet2/son'
export default {
  mixins: [mixin],
  components: {
    Son
  }
  ,
  data () {
    return {
      msg: 'color',
      miss: 'skyblue'
    }
  },
  created () { },
  methods: {
    recive () {
      console.log(' 这是father页面的消息,火箭发射成功');
    },
    change () {
      console.log('使用$listener传到grandson页面,成功了')
    }
  }
}
</script>
